import Node from "./Node";
export default class DeviceNode extends Node {
  constructor({ x, y, width, height, name, info }) {
    super();
    this.deviceType = "deviceType";
    this.width = width;
    this.height = height;
    this.borderColor = "#333";
    this.x = x;
    this.y = y;

    this.imageWidth = 80;
    this.imageHeight = 30;

    this.name = name;
    this.info = info;

    this.isDrag = false; // Ë¢´ÊãñÂä®
    this.isSelect = false;
    console.log(this);
  }
  /**
   * ÈáçÊñ∞ÊèêÁ¨îÁöÑÈÄªËæëÔºåÂè™ÊúâÂú®activeÊÉÖÂÜµÊâçËß¶ÂèëÔºå‰∏çÁÑ∂‰ºöÈôç‰ΩéÊâπÈáèÊ∏≤ÊüìÁöÑÊÄßËÉΩ
   * - Êúâ‰ªÄ‰πàÂäûÊ≥ïÂèØ‰ª•ÂáèÂ∞ëÊèêÁ¨îhover ctxË∞ÉÁî®Ê¨°Êï∞ÈóÆÈ¢ò
   *   - ÈìæË°®Ôºå‰ΩÜÈìæË°®ÁöÑÈÅçÂéÜÈÄüÂ∫¶Â¶Ç‰ΩïÂë¢Ôºü
   *   - jsËøêÁÆóÊàñËÄÖworkerËøêÁÆóÊØîÁõ¥Êé•ctxÊÄßËÉΩÊõ¥È´ò
   *
   * @param {*} ctx
   * @memberof DeviceNode
   */
  render(ctx) {
    const [a, b] = [this.x, this.y];
    const [c, d] = [this.x + this.width, this.y + this.height];
    ctx.moveTo(a, b);
    ctx.lineTo(c, b);
    ctx.lineTo(c, d);
    ctx.lineTo(a, d);
    ctx.lineTo(a, b);

    if (this.active) {
      ctx.strokeStyle = "#f80";
      ctx.stroke();
      ctx.beginPath();
    } else {
      ctx.strokeStyle = "#333";
      ctx.stroke();
      ctx.beginPath();
    }
  }
}
